@import url(https://use.typekit.net/nzc3lod.css);
@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300&display=swap);

:root{
  --light: #FFFFFF;
  --mid: #74959D;
  --black: #0F1817;
  --primary: #30544C;
  --secoundary: #A58859;
}

h1, h2, h3, h4{
  margin: 0;
}

body {
  margin: 0;
  font-family: all-round-gothic,sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #30544C;
  color: var(--primary);
}

p {
  color: #0F1817;
  color: var(--black);
  font-family: 'Poppins', sans-serif;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

nav{
  background: var(--light) 0% 0% no-repeat padding-box;
  box-shadow: 0px 3px 6px #00000029;
  opacity: 1;
  padding-top: 1.5rem;
  padding-bottom: 1.5rem;
  font-size: 24px;
}

.search {
  border: 3px solid var(--primary);
  border-radius: 24px;
  opacity: 1;
}

.searchBox {
  display: none;
  visibility: hidden;
}

.shop {
  background: var(--secoundary) 0% 0% no-repeat padding-box;
  background: #A58859 0% 0% no-repeat padding-box;
  border-radius: 24px;
  color: var(--light);
  opacity: 1;
  padding: 8px 30px 8px 30px;
}

@-webkit-keyframes searchExtend {
  from {visibility: hidden;}
  to {visibility: visible;}
}

@keyframes searchExtend {
  from {visibility: hidden;}
  to {visibility: visible;}
}

.search:hover .searchBox{
  display: flex;
  -webkit-animation-name: searchExtend;
          animation-name: searchExtend;
  -webkit-animation-duration: 4s;
          animation-duration: 4s;
}
.headerbg{

  position: relative;
  z-index: 0;
  
}

.headerbg::before {
  content: "";
  position: absolute;
  z-index: -1;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: url("/varnaka/static/media/Header Filler@2x.1287e704.png");
  background-size: cover;
  -webkit-clip-path: polygon(0% 100%, 100% 100%, 100% 0%, 80% 0%, 0% 95%);
          clip-path: polygon(0% 100%, 100% 100%, 100% 0%, 80% 0%, 0% 95%);
}

.cta {
  background: #30544C 0% 0% no-repeat padding-box;
  color: var(--light);
  font-size: 2em;
  border-radius: 30px;
  opacity: 1;
}

.f-4 {
  font-size: 4rem;
}

.pb8 {
  padding-bottom: 27rem;
}

.App {
  text-align: center;
}

